export const handleShare = () => {
  if (navigator.share) {
    navigator.share({
      title: '  ',
      text: ',    ',
      url: window.location.href
    }).then(() => {
      console.log(' ');
    }).catch((error) => {
      console.error('   :', error);
    });
  } else {
    console.log('    ');
  }
};